import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import {Link} from 'gatsby';
import { BgImage } from "gbimage-bridge"

export default function Activiteiten() {
    const { placeholderImage,  placeholderKlankschaal, placeholderAmarill1, placeholderAmarill2, placeholderEzinge, placeholderTempelCasa,
      placeholderCasaHuis, placeholderOmgevingCasa } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Activiteiten Activities.JPG"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
            placeholderKlankschaal: file(relativePath: {eq: "Klank.JPG"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderAmarill1: file(relativePath: {eq: "Amaryllis1.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderAmarill2: file(relativePath: {eq: "Amarillys2.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderEzinge: file(relativePath: {eq: "Kerkje Ezinge.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderTempelCasa: file(relativePath: {eq: "Tempel Casa de Nepal.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderCasaHuis: file(relativePath: {eq: "Casa de Nepal huis.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
            placeholderOmgevingCasa: file(relativePath: {eq: "Omgeving Casa de Nepal met Anu.jpg"}) {
                childImageSharp {
                    gatsbyImageData(
                      width: 800
                      quality: 80
                      webpOptions: {quality: 90}
                      formats: [AUTO, WEBP, AVIF]
                    )
                  }
            }
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
      const klankImage = getImage(placeholderKlankschaal);
      const AmarillImage1 = getImage(placeholderAmarill1);
      const AmarillImage2 = getImage(placeholderAmarill2);
      const Ezinge = getImage(placeholderEzinge);
      const Tempel = getImage(placeholderTempelCasa);
      const CasaHuis = getImage(placeholderCasaHuis);
      const OmgevingCasa = getImage(placeholderOmgevingCasa);

    
    return (
        <Layout>
        <BgImage image={pluginImage} className="bg"
      Tag="section">
          <div className="actHeading">
          <h1 className="actHeading">ACTIVITEITEN</h1>
          </div>
          </BgImage>
          <div className="genText">
          <p>
          Belangrijk bij alles wat we doen is dat het stroomt en dat het geen strijd is. 
          Leven en werken vanuit liefde vormen de bedding voor de stroom, die nu in de vorm van 
          Stichting Aikyam haar weg in Nederland vindt, in verbinding met andere landen. 
              </p>
              <h3>TWEEWEKELIJKSE KLANKMEDITATIE</h3>
              <p>
              Anu en Martien bespelen om de week de klankschalen bij cursuscentrum De Poort in Groningen. <br/>
              Voor meer informatie en om je aan te melden: <a href="https://www.cursuscentrumdepoort.nl/klankmeditatie.html">website cursuscentrum De Poort</a>
              </p>
              <div className="centerImage">
              <GatsbyImage image={klankImage} alt={"De klankschaal"}/>
              </div>
              <h3>ZING-IN EZINGE</h3>
              <p>
              ontspanning, stilte, verbinding en vreugde
              </p>
              <p>
              Mantrazang <br/>
              Vrijdag 12 november <br/>
              Vrijdag 10 december<br/>
              Data in 2022 volgen nog<br/>
              </p>
              <p>
              Samen mantra's zingen geeft energie en maakt blij. Jouw eigen klank gaat op in een prachtig geheel. 
              De akoestiek van het middeleeuwse kerkje in Ezinge doet de rest. Iedereen kan meedoen. 
              We zingen mantra's uit verschillende landen en tijden. 
              </p>
              <p>
              Adres: Torenstraat 46, bovenop de Wierde te Ezinge<br/>
              Van 19:15 tot 20:15 uur (inloop vanaf 19 uur)
              </p>
              <p>
              We eindigen voor wie wil met een kopje thee.<br/>
              Bijdrage in de kosten €3<br/>
              Wie een of meerdere keren mee wil doen kan zich opgeven via <a href="mailto:martien@aikyam.nl">martien@aikyam.nl</a>
              </p>
              <div className="centerImage">
              <GatsbyImage image={Ezinge} alt={"Kerkje Ezinge"}/>
              </div>
              <h3>RETRAITEHUIS IN MIDDEN-SPANJE</h3>
              <p>
              Sinds de zomer van 2021 huurt onze Spaanse zusterorganisatie We Are One een uit natuursteen en leem gebouwd huis aan de rand van een piepklein dorpje 
              dat zich in een natuurreservaat ten noorden van Madrid bevindt: La Casa de Nepal. 
              </p>
              <p>
              Daar verzorgen we samen met We Are One korte en langere retraites waarin je ervaart hoe het is om op een simpele en harmonieuze wijze te leven, dichtbij de natuur. <br/>
              Een willekeurige dag in Casa de Nepal: <br/>
              Vanaf 6:20 uur stiltemeditatie tot ongeveer half 8, gevolgd door mantrazang <br/>
              Korte pauze <br/>
              Yoga van 9 tot 10:30 uur<br/>
              Vers fruit<br/>
              Warme maaltijd om 11 uur<br/>
              Vrije tijd om in de prachtige bergachtige omgeving te wandelen of mandala's te tekenen op de veranda of...<br/>
              Avondmaaltijd om 19 uur<br/>
              Vanaf 20 uur stiltemeditatie van ongeveer 45 minuten waarna we nog een half uur of langer samen mantra's zingen<br/>
              Stilte, tot de volgende ochtend...
              </p>
              <p>
              Wil je meer weten? Neem dan <Link to="/contact">contact</Link>  met ons op.
              </p>
              <div className="centerImage">
              <GatsbyImage image={Tempel} alt={"Tempel Casa de Nepal"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={CasaHuis} alt={"Casa de Nepal huis"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={OmgevingCasa} alt={"Omgeving Casa de Nepal"}/>
              </div>

              <h3>AMARYLLIS PROJECT</h3>
              <p>
              Guru Premangee is betoverd door de schoonheid van de Amaryllis bloemen. Hij wil ze in Nepal gaan kweken en verspreiden, ‘want’,
              zo zegt hij, ‘Nepalese kantoren en huizen hebben meer kleurrijke bloemen nodig!’ Op dit moment is daar maar één soort te krijgen
             dus er is ruimte genoeg voor meer!
              </p>
              <p>
              Om amaryllissen goed te kunnen kweken heb je een stabiele omgeving nodig. 
              Het is daarom noodzakelijk om ze in kassen te kweken met een goed bewateringssysteem. 
            Dat is best een uitdaging in Nepal. 
              </p>
              <p>
              Bezit jij over de juiste kennis om amaryllissen te kweken en/of kassen te ontwerpen in 
              landen met zeer beperkte middelen? Of wil je financieel bijdragen aan dit project? 
                Dan komen we graag met je in <Link to="/contact">contact!</Link>
              </p>
              <div className="centerImage">
              <GatsbyImage image={AmarillImage1} alt={"Amaryllis"}/>
              </div>
              <div className="centerImage">
              <GatsbyImage image={AmarillImage2} alt={"Amaryllis 2"}/>
              </div>

              </div>
        </Layout>
    )
}
